import React from "react";
import Helmet from 'react-helmet'
import Link from 'gatsby-link'

import ReadingProgressBar from "../components/ReadingProgressBar";
import PostContent from "../components/PostConent"
import FloatingAction from "../components/FloatingAction"
import Disqus from "../components/Disqus";

const siteConfig = require('../../site-config.json');

export default class PostTemplate extends React.Component {
  constructor(props){
    super(props);

    this.state = {
      tocShow: false
    }

    this.tocHandler = this.tocHandler.bind(this);
    this.showTocHandler = this.showTocHandler.bind(this);
    
  }

  componentDidMount() {
    if(window.tocbot) {
      window.tocbot.init({ tocSelector: '.js-toc',
        // Where to grab the headings to build the table of contents.
        contentSelector: '.js-toc-content',
        // Which headings to grab inside of the contentSelector element.
        headingSelector: 'h2, h3',
        collapseDepth: 6
      });
    }
  }

  tocHandler(event) {
    event.persist();
    if(event.target.tagName=='A') {
      this.setState({tocShow: !this.state.tocShow})
    }
  }

  showTocHandler() {
    this.setState({tocShow: !this.state.tocShow})
  }

  render() {
    const { markdownRemark } = this.props.data; // data.markdownRemark holds our post data
    const { frontmatter, html, timeToRead, excerpt } = markdownRemark;
    const pathContext = this.props.pathContext;

    const post={
      title: frontmatter.title,
      url: pathContext.postUrl,
      category_id: frontmatter.category
    }
    return (
      <div>
        <Helmet>        
          <script src="/tocbot.js" defer></script>
        </Helmet>
        <ReadingProgressBar />
        <PostContent title={frontmatter.title} html={html} publishDate={frontmatter.date} timeToRead={timeToRead}
          postUrl={pathContext.postUrl} excerpt={excerpt} prePost={pathContext.prePost} nextPost={pathContext.nextPost}/>
        <div className={`js-toc ${ this.state.tocShow? 'tocShow' : ''}`} onClick={this.tocHandler}></div>
        <FloatingAction postUrl={pathContext.postUrl} excerpt={excerpt} title={frontmatter.title}
          onShowToc = {this.showTocHandler}/>
        <Disqus
          post={post} disqusShortname='thapganblog'
        />
      </div>
    );
  }
}

export const pageQuery = graphql`
  query BlogPostByPath($fileAbsolutePath: String!) {
    markdownRemark(fileAbsolutePath: { eq: $fileAbsolutePath } ) {
      html
      excerpt(pruneLength: 250)
      timeToRead
      fileAbsolutePath
      frontmatter {
        date(formatString: "DD-MM-YYYY")
        title
      }
    }
  }
`;

