import React from 'react'
import Link from 'gatsby-link'
//import ReactGesture from 'react-gesture'
import Gesture from 'rc-gesture'
import { navigateTo } from 'gatsby-link'
import "./post-content.css"

export default function PostContent({ title, html, publishDate, timeToRead, postUrl, excerpt, prePost, nextPost }) {
    return (
        <Gesture
            onSwipeLeft={()=>{
                if(prePost) navigateTo(prePost.url);
            }}
            onSwipeRight={()=>{
                if(nextPost) navigateTo(nextPost.url);
            }}
        >
        <article className="blog-post">
            {(nextPost? <div className="post-navigator next-post"><Link to={nextPost.url}><i className="fas fa-caret-right"></i></Link></div>: "")}
            {(prePost? <Link className="post-navigator pre-post" to={prePost.url}><i className="fas fa-caret-left"></i></Link> : "")}
            <h1 className="blog-post-title">{title}</h1>
            <p className="blog-post-meta"><time dateTime={publishDate} itemProp="datePublished">{publishDate}</time> | {timeToRead} Minute Read</p>
            <div
                className="blog-post-content js-toc-content"
                dangerouslySetInnerHTML={{ __html: html }}
            />
        </article>
        </Gesture>
    )
}



